<template>
<div style="text-align:left;">
  <div id="map"></div>
  <p>
    <span>Zoom Value : {{ zoomValue }}  </span>
    <el-button type="primary" @click="zoomUp"></el-button>
    <el-button type="primary" @click="zoomDowm"></el-button>
  </p>

  <!--  -->
  <template id="controls">
    <el-collapse id="checkbox-board" accordion v-model="controlBoardActive" @change="onControlBoardClick">
      <el-collapse-item name="1">
        <div style="background-color:rgb(0, 0, 0, 0);">
          <el-checkbox-group v-model="controlList">
            <el-checkbox label="Mouse Position" checked></el-checkbox><br/>
            <el-checkbox label="Attribution"></el-checkbox><br/>
            <el-checkbox label="Rotate" checked></el-checkbox><br/>
            <el-checkbox label="Full Screen" checked></el-checkbox><br/>
            <el-checkbox label="Overview Map" checked></el-checkbox><br/>
            <el-checkbox label="Scale Line" checked></el-checkbox><br/>
            <el-checkbox label="Zoom" checked></el-checkbox><br/>
            <el-checkbox label="Zoom Slider" checked></el-checkbox><br/>
            <el-checkbox label="Zoom To Extent"></el-checkbox>
          </el-checkbox-group>
        </div>
      </el-collapse-item>
    </el-collapse>
  </template>
</div>
</template>

<script>
import $ from 'jquery';
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import Control from 'ol/control/Control';

import {format, toStringHDMS, degreesToStringHDMS} from 'ol/coordinate.js';
import {defaults as defaultControls,
        MousePosition,
        Attribution,
        Rotate,
        FullScreen,
        OverviewMap,
        ScaleLine,
        Zoom,
        ZoomSlider,
        ZoomToExtent} from 'ol/control.js';

export default {
  name: 'map-control',
  watch: {
    zoomValue: function (val, oldVal) {
      if (val > 28) {
        this.zoomValue = new Number(28);
      } else if (val < 0) {
        this.zoomValue = new Number(0);
      }
    },
    controlList: function (val, oldVal) {
      if (val === oldVal) return;
      // New
      if (val.includes('Mouse Position') && !oldVal.includes('Mouse Position')) this.map.addControl(this.mapControls.mousePositionControl);
      // Move
      else if (!val.includes('Mouse Position') && oldVal.includes('Mouse Position')) this.map.removeControl(this.mapControls.mousePositionControl);

      if (val.includes('Attribution') && !oldVal.includes('Attribution')) this.map.addControl(this.mapControls.attributionControl);
      else if (!val.includes('Attribution') && oldVal.includes('Attribution')) this.map.removeControl(this.mapControls.attributionControl);

      if (val.includes('Rotate') && !oldVal.includes('Rotate')) this.map.addControl(this.mapControls.rotateControl);
      else if (!val.includes('Rotate') && oldVal.includes('Rotate')) this.map.removeControl(this.mapControls.rotateControl);

      if (val.includes('Full Screen') && !oldVal.includes('Full Screen')) this.map.addControl(this.mapControls.fullScreenControl);
      else if (!val.includes('Full Screen') && oldVal.includes('Full Screen')) this.map.removeControl(this.mapControls.fullScreenControl);

      if (val.includes('Overview Map') && !oldVal.includes('Overview Map')) {
        this.map.addControl(this.mapControls.overviewMapControl);
        $(this.mapControls.scaleLineControl.element).removeClass('ol-scale-line-left');
      } else if (!val.includes('Overview Map') && oldVal.includes('Overview Map')) {
        this.map.removeControl(this.mapControls.overviewMapControl);
        $(this.mapControls.scaleLineControl.element).addClass('ol-scale-line-left');
      }

      if (val.includes('Scale Line') && !oldVal.includes('Scale Line')) this.map.addControl(this.mapControls.scaleLineControl);
      else if (!val.includes('Scale Line') && oldVal.includes('Scale Line')) this.map.removeControl(this.mapControls.scaleLineControl);

      if (val.includes('Zoom') && !oldVal.includes('Zoom')) this.map.addControl(this.mapControls.zoomControl);
      else if (!val.includes('Zoom') && oldVal.includes('Zoom')) this.map.removeControl(this.mapControls.zoomControl);

      if (val.includes('Zoom Slider') && !oldVal.includes('Zoom Slider')) this.map.addControl(this.mapControls.zoomSliderControl);
      else if (!val.includes('Zoom Slider') && oldVal.includes('Zoom Slider')) this.map.removeControl(this.mapControls.zoomSliderControl);

      if (val.includes('Zoom To Extent') && !oldVal.includes('Zoom To Extent')) this.map.addControl(this.mapControls.zoomToExtentControl);
      else if (!val.includes('Zoom To Extent') && oldVal.includes('Zoom To Extent')) this.map.removeControl(this.mapControls.zoomToExtentControl);
    },
  },
  data() {
    return {
      map: {},
      mapHeight: '',
      layers: [],
      view: {},
      zoomValue: 0,

      // Controls
      mapControls: {
        mousePositionControl: {},
        attributionControl: {},
        rotateControl: {},
        fullScreenControl: {},
        overviewMapControl: {},
        scaleLineControl: {},
        zoomControl: {},
        zoomSliderControl: {},
        zoomToExtentControl: {}
      },
      controlList: [],
      isOverviewMapCollapsed: true,
      isFullScreen: false,
      controlBoardActive: '1',
    }
  },//data
  methods: {
    createCoordinateString (coordinate) {
      return 'Mouse Position: ( ' + degreesToStringHDMS('EW', coordinate[0], 2) + ', '
        + degreesToStringHDMS('NS', coordinate[1], 2) + ' )';
    },
    preDefine() {
      // Map Height
      let h = $('.content-box').css('height');
      this.mapHeight = (h.split('px')[0] - 38).toString() + 'px';
      $('#map').css('height', this.mapHeight);

      // layers
      this.layers = [
        new TileLayer({
          source: new OSM()
        }),
      ],
      // view
      this.view = new View({
        center: [114.353741, 30.528813],
        zoom: 4,
        projection: 'EPSG:4326',
      });
      // Default Controls
      this.mapControls.mousePositionControl = new MousePosition({
        coordinateFormat: this.createCoordinateString,
        projection: 'EPSG:4326',
        className: 'custom-mouse-position'
      });
      this.mapControls.attributionControl = new Attribution();
      this.mapControls.rotateControl = new Rotate({
        className: 'ol-rotate',
        autoHide: true
      });
      this.mapControls.fullScreenControl = new FullScreen({
        className: 'ol-full-screen'
      });
      this.mapControls.overviewMapControl = new OverviewMap({
        view: new View({
          projection: 'EPSG:4326'
        })
      });
      this.mapControls.scaleLineControl = new ScaleLine();
      this.mapControls.zoomControl = new Zoom();
      this.mapControls.zoomSliderControl = new ZoomSlider();
      this.mapControls.zoomToExtentControl = new ZoomToExtent();
    },
    createMap() {
      this.map = new Map({
        controls: defaultControls({
          attribution: false,
          zoom: false,
          rotate: false,
        }).extend([
          this.mapControls.mousePositionControl,
          // this.mapControls.attributionControl,
          this.mapControls.rotateControl,
          this.mapControls.fullScreenControl,
          this.mapControls.overviewMapControl,
          this.mapControls.scaleLineControl,
          this.mapControls.zoomControl,
          // this.mapControls.zoomToExtentControl,
          this.mapControls.zoomSliderControl
        ]),
        target: 'map',
        layers: this.layers,
        view: this.view
      });
      this.zoomValue = this.map.getView().getZoom();
    },
    zoomUp() {
      this.map.getView().setZoom(++this.zoomValue);
    },
    zoomDowm() {
      this.map.getView().setZoom(--this.zoomValue);
    },
    addListener() {
      const controls = this.mapControls;
      // 
      this.map.on('postrender', e => {
        this.zoomValue = parseInt(e.map.getView().getZoom());
      });
      $(controls.overviewMapControl.element).children('button').click( _ => {
        this.isOverviewMapCollapsed = !this.isOverviewMapCollapsed;
        $(controls.scaleLineControl.element).toggleClass('ol-scale-line-right');
      });
      $(controls.fullScreenControl.element).children('button').click( _ => {
        this.isFullScreen = !this.isFullScreen;
        $('#map').css('height', this.isFullScreen ? '100%' : this.mapHeight);
      });
    },
    addCustomControl () {
      this.map.addControl(new Control({
        element: $('#checkbox-board')[0]
      }));
    },
    onControlBoardClick (isActived) {
      const fullScreen = this.mapControls.fullScreenControl.element,
            rotate =this.mapControls.rotateControl.element;
      
      // "1" 
      if (isActived === '1') {
        // 1. Add left-moved animation
        $(fullScreen).toggleClass('anim-full-screen-left');
        $(rotate).toggleClass('anim-rotate-left');

        // 2. Set the position
        $(fullScreen).removeClass('ol-full-screen-right');
        $(rotate).removeClass('ol-rotate-right');
        
        // 3. Remove left-moved animation
        setTimeout(_ => {
          $(fullScreen).toggleClass('anim-full-screen-left');
          $(rotate).toggleClass('anim-rotate-left');
        }, 500);
      } else {
        // 1. Add right-moved animation
        // 2. Set the position
        setTimeout(() => {
          $(fullScreen).toggleClass('anim-full-screen-right');
          $(rotate).toggleClass('anim-rotate-right');

          $(fullScreen).addClass('ol-full-screen-right');
          $(rotate).addClass('ol-rotate-right');
        }, 300);

        // 3. Remove right-moved animation
        setTimeout(_ => {
          $(fullScreen).toggleClass('anim-full-screen-right');
          $(rotate).toggleClass('anim-rotate-right');
        }, 500);
      }
    }
  }, // methods
  mounted() {
    this.preDefine();
    this.createMap();
    this.addCustomControl();
    this.addListener();
  }, // mounted
  beforeDestroy() {
    const map = this.map;
    this.controlList.forEach( control => {
      map.removeControl(control);
    });
  }, // beforeDestroy
};
</script>

<style scoped>
#map {
  width: 100%;
}
</style>

<style>
p {
  margin: 3px 0;
}
.custom-mouse-position {
  bottom: 8px;
  right: 8px;
  position: absolute;
  font-weight: bold;
}
/* Full Screen */
.ol-full-screen {
  right: 145px;
}
.ol-full-screen-right {
  right: 40px;
}
.anim-full-screen-left {
  animation: anim-full-screen-left 0.3s;
}
.anim-full-screen-right {
  animation: anim-full-screen-right 0.3s;
}
@keyframes anim-full-screen-right {
  from {right: 145px;}
  to {right: 40px;}
}
@keyframes anim-full-screen-left {
  from {right: 40px;}
  to {right: 145px;}  
}
/* Rotate */
.ol-rotate {
  right: 180px;
}
.ol-rotate-right {
  right: 75px;
}
.anim-rotate-left {
  animation: anim-rotate-left 0.3s;
}
.anim-rotate-right {
  animation: anim-rotate-right 0.3s;
}
@keyframes anim-rotate-right {
  from {right: 180px;}
  to {right: 75px;}
}
@keyframes anim-rotate-left {
  from {right: 75px;}
  to {right: 180px;}
}
/* Scale Line */
.ol-scale-line {
  left: 45px;
}
.ol-scale-line-left {
  left: 8px;
}
.ol-scale-line-right {
  left: 175px;
}
#checkbox-board {
  display: inline-block;
  padding: 1px;
  position: absolute;
  background-color: rgb(0, 0, 0, 0);
  top: 0.5em;
  right: 8px;
}
.el-checkbox__input.is-checked+.el-checkbox__label {
  color: rgb(6, 41, 77);
  font-weight: bold;
}
.el-collapse-item__header {
  margin: 1px;
  width: 25px;
  height: 25px;
  background-color: rgb(240, 255, 255, 0.5);
}
.el-collapse-item__header.is-active {
  width: 100%;
}
.el-collapse-item__content {
  background-color: rgb(240, 255, 255, 0.5);
  padding: 3px;
}
.el-collapse-item__wrap {
  background-color: rgb(0, 0, 0, 0);
  width: 135px;
}
</style>